@use '../../global/style-vars.scss';

.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-width: 100vw;
  // background-color: rgb(0, 1, 17);

  div {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 2rem;
  }

  p {
    width: 80%;
    text-align: left;
    font-size: 16px;
    font-weight: 100;
  }

  img {
    transition: all 7s ease;
    transform: scale(.8);
    width: 60vw;
    border-radius: 100%;
    filter: saturate(0);
    box-shadow: 0px 0px 100px rgba(255, 0, 255, 0);

    &:hover {
      transform: scale(1) rotate(-2deg);
      filter: saturate(.5);
      box-shadow: 0px 0px 100px rgba(255, 0, 255, .3);
    }
  }

  .down-arrow {
    transition: all .2s ease-in-out;
    margin-top: 2rem;
    transform: scale(1);
    width: 40px;

    &:hover {
      transform: scale(1.4);
      filter: brightness(3);
    }
  }
}

@media screen and (min-width: 769px) {
  .home {
    div {
      flex-direction: row;
    }

    p {
      width: 40vw;
    }

    img {
      width: 30vw;
    }

    .down-arrow {
      width: 90px;
    }
  }

  @media (orientation: portrait) {
    .home {
      justify-content: center;

      div {
        flex-direction: column;
      }

      img {
        margin-top: 0;
        width: 35vw;
        background-color: #fff;
      }
    }
  }
}