.custom-button {
  cursor: pointer;
  appearance: none;
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

.menu-button {
  transition: all .2s ease-out;
  font-size: 1rem;
  color: white;
  margin: 1rem;
  transform: skew(-15deg);

  &:active {
    color: rgba(0, 1, 17, .7);
  }
}

.form-button {
  transition: all .2s ease-in-out;
  font-size: 1rem;
  padding: .3rem 1rem;
  border-radius: 10px;
  color: rgba(205, 100, 144);
  color: black;
  background-color: rgba(255, 255, 255, 0.9);

  &:active {
    background-color: rgba(205, 100, 144, .8);
    color: rgb(251, 247, 188);
  }
}

.toggle-button {
  font-size: 16px;
  padding: 5px 8px;
  border-radius: 5px;
  border: 1px solid white;
  color: white;
}

.selected {
  background-color: white;
  color: rgba(255, 0, 149, .8);
}

.project-button {
  font-size: 16px;
  margin-bottom: 0.7rem;
  color: rgba(255, 0, 149, .8);
  border: 1px solid rgba(255, 0, 149, .8);
  border-radius: 5px;
}


@media screen and (min-width: 769px) {
  .menu-button {
    font-size: .8rem;
    margin: 0;
    margin-left: 1rem;

    &:hover {
      color: rgba(255, 0, 149, 0.884);
    }

    &:active {
      color: rgba(0, 1, 17, .7);
    }
  }
  .form-button {
    &:hover {
      background-color: rgba(0, 0, 0, .8);
      color: rgb(255, 255, 255);
    }
  }
}

@media screen and (min-width: 910px) {
  .menu-button {
    font-size: 1rem;
  }
}