@use '../../global/style-vars.scss';

.about {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 100vw;
  background: linear-gradient(35deg,
      rgba(255, 0, 149, .5) 10%,
      rgb(255, 0, 149, .8) 50%,
      rgba(17, 0, 255, 0.5) 50%,
      rgba(17, 0, 255, .1) 90%);

  .top-bar {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: flex-end;

    .about-header {
      display: flex;
      justify-content: flex-end;
      align-items: flex-end;
      margin: 4rem style-vars.$window-margin-mobile 0;
  
      .section-title {
        font-size: 30px;
        font-weight: 900;
        transform: skew(-6.9deg);
        margin: 0;
        margin-right: 15px;
      }
  
      .about-icon {
        transition: all .2s ease-in-out;
      }
    }
    .line-break {
      position: absolute;
      bottom: 10px;
      right: 0;
      height: 1px;
      width: 63%;
      margin-right: 5.5rem;
      background-color: white;
    }
  }

  .text-container {
    width: calc(100vw - 2rem);
    height: 70%;
    overflow-y: scroll;
    overflow-x: hidden;
    background-color: rgba(0, 0, 0, .5);
    border-radius: 10px;
    margin-top: 1rem;

    p {
      margin: 7px style-vars.$window-margin-mobile;
    }
  }
}

@media screen and (min-width: 769px) {
  .about {
    .top-bar {
      
      .about-header {
        margin: 10rem style-vars.$window-margin 0;
      }
      .line-break {
        width: 30%;
        margin-right: 12rem;
      }
    }
    .text-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      overflow: hidden;
      height: auto;
      width: calc(100vw - 16rem);
      padding: 0;

      p {
        margin: 7px style-vars.$window-margin-mobile;
      }
    }
  }
  @media (orientation: portrait) {
    
  }
}