@use '../../global/style-vars.scss';

.contact {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  min-width: 100vw;
  color: white;
  background: linear-gradient(35deg,
      rgba(255, 0, 149, .5) 10%,
      rgb(255, 0, 149, .8) 50%,
      rgba(17, 0, 255, 0.5) 50%,
      rgba(17, 0, 255, .1) 90%);

  .contact-header {
    margin-top: 4rem;
    
    .contact-header-text {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      margin: 0rem style-vars.$window-margin-mobile;
      
      .section-title {
        font-size: 30px;
        font-weight: 900;
        transform: skew(-6.9deg);
        margin: 0;
        margin-right: 15px;
      }
  
      .contact-icon {
        transition: all .2s ease-in-out;
      }
    }
  }

  .icon-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 50%;

    .github, .linkedin, .email {
      transition: all .2s ease-in-out;
      width: 92px;

      &:hover {
        transform: scale(1.2);
      }
    }
  }
}

@media screen and (min-width: 768px) {
  .contact {

    .contact-header {
      margin: 10rem style-vars.$window-margin 0;
    }

    .line-break {
      margin-right: 12rem;
    }

    .contact-text {
      // margin-right: 12rem;
      width: 50%;
    }

    .icon-container {
      flex-direction: row;
      justify-content: center;
      align-items: flex-start;

      .github, .linkedin, .email {
        margin-right: 2rem;
      }
    }
  }
}