@use '../../global/style-vars.scss';

.work {
  position: relative;
  display: flex;
  flex-direction: column;
  color: #FFF;
  min-width: 100vw;
  background: linear-gradient(325deg,
      rgba(17, 0, 255, 0.5) 0%,
      rgba(17, 0, 255, .1) 50%,
      rgba(255, 0, 149, .5) 50%,
      rgba(255, 0, 149, .8) 90%);

  .top-bar {
    position: relative;

    .work-header {
      margin: 4rem style-vars.$window-margin-mobile 0;
      display: flex;
      align-items: center;

      .section-title {
        font-size: 30px;
        font-weight: 900;
        transform: skew(-6.9deg);
        margin: 0;
        margin-left: 15px;
      }

      .work-icon {
        transition: all .2s ease-in-out;
      }
    }

    .line-break {
      position: absolute;
      bottom: 13px;
      left: 5px;
      height: 1px;
      width: 63%;
      margin-left: 5rem;
      background-color: white;
    }
  }

  .button-container {
    display: flex;
    justify-content: space-between;
    margin: .8rem style-vars.$window-margin-mobile 0;
  }

  .portfolio-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;

    .portfolio {
      margin-top: 1rem;
      width: 90vw;
      height: 60vh;
      overflow-y: scroll;

      .project-card {
        display: flex;
        flex-direction: column;
        border-radius: 10px;
        padding: 10px;
        background-color: rgba(0, 0, 0, .5);
        margin: 2rem 0;

        h2 {
          margin: 0;
          margin-bottom: .5rem;
        }

        img {
          width: 100%;
        }

        a {
          color: rgba(255, 0, 149, .8);
          text-decoration: none;
        }
      }
    }
  }
}

@media screen and (min-width: 768px) {
  .work {
    .top-bar {

      .work-header {
        margin: 5rem style-vars.$window-margin 0;
      }

      .line-break {
        margin-left: 12rem;
        width: 30%;
      }
    }

    .work-text {
      margin-left: 12rem;
    }

    .button-container {
      margin: .8rem style-vars.$window-margin;
      justify-content: flex-start;

      button {
        margin-right: 1rem;
      }
    }

    .portfolio-container {
      margin: 0 style-vars.$window-margin;

      .portfolio {
        display: flex;
        overflow: hidden;
        width: 100%;
        height: auto;
        margin-top: 0;

        .project-card {
          height: auto;
          width: 320px;
          margin-right: 1rem;
          margin-top: 0;
        }
      }
    }
  }

  @media (orientation: portrait) {
    .work {
      .top-bar {
        .work-header {
          margin: 5rem style-vars.$window-margin-tablet 0;
        }

        .line-break {
          bottom: 14px;
          left: 55px;
          margin-left: 5rem;
        }
      }
      .button-container {
        margin: .8rem style-vars.$window-margin-tablet;
      }
      .portfolio-container {
        margin: 0 style-vars.$window-margin-tablet;
      }
    }
  }
}

.desc-transition-enter {
  opacity: 0;
}

.desc-transition-enter-active {
  opacity: 1;
  transition: all .2s ease-in-out;
}

.desc-transition-exit {
  opacity: 1;
}

.desc-transition-exit-active {
  opacity: 0;
  transition: all .2s ease-in-out .5s;
}