  // -ms-overflow-style: none;

  // & ::-webkit-scrollbar {
  //   width: 0 !important;
  //   display: none;
  // }
  
  .page-container {
    height: 100%;
    width: 100vw;
  }
}

.shadow {
  position: fixed;
  z-index: -1;
  top: 100vh;
  width: 100vw;
  height: 1px;
  box-shadow: 0px -90vh 50vh 10px rgba(255, 0, 255, 0.9);
}

.shadow2 {
  position: fixed;
  z-index: -1;
  bottom: -100vh;
  width: 100vw;
  height: 1px;
  box-shadow: 0px -90vh 50vh 10px rgba(255, 0, 255, 0.9);
}

.rotate {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, .95);
  color: rgba(255, 0, 255, 0.9);
  font-size: 26px;
  z-index: 999;
  
  .rotate-icon {
    animation: rotate 2s ease-in-out infinite;
    width: 70px;
  }
}

@keyframes rotate {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.4);
  }
}