.header-container {
  position: fixed;
  display: flex;
  flex-direction: column;
  z-index: 9;

  .header {
    width: 100%;
    height: 3.2rem;
    display: flex;
    align-items: center;
    box-shadow: 0px -250px 300px rgba(255, 0, 255, 0.884); 
    background-color: rgba(0, 1, 17, .4);
    backdrop-filter: blur(10px);

    .logo {
      width: 20px;
      margin: 1em;
      transition: all .3s ease-out;
      cursor: pointer;
      fill: white;
    }

    .logo:hover {
      transform: scale(1.3) rotate(90deg);
      fill: rgba(255, 0, 149, .884)
    }

    .title {
      font-size: 16px;
      transition: all .3s ease-out;
      transform: skew(-6.9deg);
      cursor: pointer;
    }

    .title:hover {
      transform: skew(0deg);
      color: rgba(255, 0, 149, 0.884)
    }

    .menu {
      margin-right: 2rem;
      margin-left: auto;
    }

    .hamburger-container {
      // transition: all 1s ease-in-out;
      margin-left: auto;
      color: white;

      &.open {
        color: rgba(255, 0, 149, .884)
      }
    }
  }

  .header-border {
    height: 1px;
    width: 100vw;
    background: linear-gradient(90deg, rgba(255, 0, 149, .1) 5%, rgba(255, 0, 149, .5) 50%, rgba(255, 0, 149, .1) 90%)
  }
}


@media screen and (min-width: 769px) {
  .header-container {
    .header {
      .title {
        font-size: 20px;
      }

      .logo {
        width: 35px;
      }
    }
  }
}

@media screen and (min-width: 910px) {
  .header-container {

    .header {
      height: 4rem;

      .logo {
        width: 30px;
        margin: 1.5em;
      }

      .title {
        font-size: 24px;
        margin-left: 1rem;
      }

      .menu {
        margin-right: 2rem;
        margin-left: auto;
      }
    }
  }
}